import React from "react";
import style from "../Styles/location.module.css";
import AOS from "aos";
import "aos/dist/aos.css";

AOS.init({
  offset: 200,
  duration: 800,
  easing: 'ease-in-quad',
  delay: 100,
});

function Location() {
  return (
    <div data-aos = "fade in" className={style.page}>
      <div class={style.headings}>WHERE TO GO</div>

    </div>
  );
}
export default Location;